* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body, html {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 24px;
}


.App {
    display: grid;
    place-items: center;
    height: 100vh;
    width: 100%; 
    position: relative;
 }

 input {
     font-size: inherit;
     padding-left: 0.5rem;
     padding-right: 1rem;
     height: 2rem;
     width: 20rem;
     overflow-wrap: break-word;
     border: 1px solid gray;
     position: absolute;
     border-radius: 50px;
     word-wrap: break-word;
     background-color: transparent;
     -webkit-border-radius: 50px;
     -moz-border-radius: 50px;
     -ms-border-radius: 50px;
     -o-border-radius: 50px;
}

#search-bar2 {
    z-index: -1;
    color: gray;
    cursor: none;
}


textarea:focus, input:focus {
    outline: none;
}

@media only screen and (min-device-width: 240px) and (max-device-width: 720px) {
    body, html {
        font-size: 15px;
    }
}